/* dm-sans-regular - latin */
@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/dm-sans-v5-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('DM Sans Regular'), local('DMSans-Regular'),
    url('../fonts/dm-sans-v5-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/dm-sans-v5-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/dm-sans-v5-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('../fonts/dm-sans-v5-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../fonts/dm-sans-v5-latin-regular.svg#DMSans') format('svg'); /* Legacy iOS */
}
